.section {
	position: relative;
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	margin: 50px 0;

	&.has-background {
		margin: 0;
		padding: 50px 0;
	}
}

.content {
    padding: $component_margin*1.5 0;

    @include Tablet {
        padding: 40px 0;
    }

    @include breakpoint(desktop) {
        padding: $component_margin*4 0;
    }

    &--single {
        padding: 0;
    }

    &--dark {
        background: #171717;
        color: #fff;

        .big-p {
            opacity: 0.7;
        }

        p {
            margin-bottom: 48px;
            @include breakpoint($to: desktop) {
                margin-bottom: 24px;
            }
        }
    }

    &.help {

        @include breakpoint(desktop) {
            padding-top: 80px;
            padding-bottom: 80px;
        }
    }

    &--for-wine {
        padding-top: 0;
    }
}

/* Sidebar */
.sidebar {
    color: #0B0B0B;
}